// src/components/Login.js
import React from "react";
import { useDispatch } from "react-redux";
import { loginStart, loginSuccess, loginFailure } from "../redux/authSlice";
import { auth, provider, signInWithPopup, db } from "../services/firebase";
import { doc, setDoc, getDoc } from "firebase/firestore";

const Login = () => {
    const dispatch = useDispatch();

    const handleGoogleLogin = async () => {
        console.log(' ...');
        dispatch(loginStart());
        try {
            const result = await signInWithPopup(auth, provider);
            console.log('result', result);

            //    
            const user = {
                uid: result.user.uid,
                displayName: result.user.displayName,
                email: result.user.email,
                photoURL: result.user.photoURL,
            };

            // ,      
            const userRef = doc(db, "users", user.uid);
            console.log('userRef', userRef);

            const userDoc = await getDoc(userRef);
            console.log('userDoc', userDoc);


            if (!userDoc.exists()) {
                //   ,      
                try {
                    await setDoc(userRef, {
                        displayName: user.displayName,
                        email: user.email,
                        photoURL: user.photoURL,
                        nofap_timestamp: '0',
                        nofap_links: [
                            {
                                link: 'https://drive.google.com/file/d/1pwPKr-wq0yw-ZTyHwGxOyqOit9_BXXEV/view?usp=drive_link', note: '    '
                            },
                            {
                                link: 'https://www.youtube.com/watch?v=CwGQiTCrqEA&list=PLB5lKOLFXiEfstpsfl7UcdvwBV6TpqG7G&pp=gAQB', note: ',  '
                            },
                        ]

                    });
                } catch (error) {
                    console.log('error is', error);

                }

            }

            dispatch(loginSuccess(user)); //    
        } catch (error) {
            console.error(' :', error.message);
            dispatch(loginFailure(error.message));
        }
    };

    return (
        <div
            // className="login_button"
            style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                height: "100%",
            }}
        >
            <button onClick={handleGoogleLogin}
                className="login_button"
            >
                  Google
            </button>
        </div>
    );
};

export default Login;