import React, { useEffect, useState } from "react";
import { addLinkToUser_nofap, getUserData, removeLinkFromUser_nofap, updateUserData } from "../services/firebase";
import { useSelector } from "react-redux";


const formatTimestamp = (timestamp) => {
    const date = new Date(timestamp);
    const day = date.getDate();
    const month = date.toLocaleString("ru", { month: "long" }); //    
    const year = date.getFullYear();
    return `${day} ${month} ${year}`;
};


const calculateTimeDifference = (timestamp) => {
    const now = Date.now(); //    
    const difference = Math.abs(now - timestamp); //   

    //    , ,   
    const days = Math.floor(difference / (1000 * 60 * 60 * 24));
    const hours = Math.floor((difference % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
    const minutes = Math.floor((difference % (1000 * 60 * 60)) / (1000 * 60));
    const seconds = Math.floor((difference % (1000 * 60)) / 1000);

    //    
    return `${days} ${hours} ${minutes} ${seconds}`;
};
const ProgressBar = ({ timestamp }) => {
    //        
    const calculateProgressBoxes = (timestamp) => {
        const now = Date.now(); //    
        const difference = Math.abs(now - timestamp); //   

        //  
        const fullDays = Math.floor(difference / (1000 * 60 * 60 * 24));

        //     ( )
        const remainingTime = (difference % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60 * 24);

        return { fullDays, remainingTime };
    };

    const { fullDays, remainingTime } = calculateProgressBoxes(timestamp);

    //  
    const totalBoxes = fullDays + (remainingTime > 0 ? 1 : 0); //   
    const boxes = Array.from({ length: totalBoxes }, (_, index) => {
        if (index < fullDays) {
            return { filled: 1 }; //   
        } else {
            return { filled: remainingTime }; //   
        }
    });

    return (
        <div className="progressBar_noFap">

            <div style={{
                display: 'grid',
                // justifyContent: 'flex-start'
                gridTemplateColumns: '1fr 1fr',
                lineHeight: '50px,'

            }}>
                <p



                >
                     :<br />
                    <span style={{
                        lineHeight: '50px'
                    }} className="light_green" > {formatTimestamp(timestamp)}</span>
                </p>

                <p>
                      -- : <br />
                    <span style={{
                        lineHeight: '50px'
                    }} className="light_green">{calculateTimeDifference(timestamp)}</span>
                </p>

            </div>

            <div className="progressBar_green_boxes_wrap">
                {boxes.map((box, index) => (
                    <div className="progressBar_green_box_wrap">
                        <div
                            key={index}
                            className="progressBar_green_box"
                            style={{
                                // width: "100px",
                                // height: "20px",
                                // margin: "2px",
                                background: box.filled === 1
                                    ? "#00ff15" //   
                                    : `linear-gradient(to right, #00ff15 ${box.filled * 100}%, transparent ${box.filled * 100}%)`, //   
                            }}
                        ></div>

                    </div>
                ))}

                <div className="progressBar_green_box_wrap">
                    <div

                        className="progressBar_green_box"
                        style={{
                            // width: "100px",
                            // height: "20px",
                            // margin: "2px",
                            // background: 'transparent'
                        }}
                    ></div>

                </div>
            </div>
        </div>
    );
};


const NoFapPage = () => {

    const user = useSelector((state) => state.auth.user); //   




    //    
    const [nofap_timestamp, set_nofap_timestamp] = useState(0)
    const [isLoading, setIsLoading] = useState(true)

    const [startDate, setStartDate] = useState("");
    const [startTime, setStartTime] = useState("");
    const [isAbstaining, setIsAbstaining] = useState(false);
    const [links, setLinks] = useState([]);
    const [newLink, setNewLink] = useState("");
    const [newLinkNote, setNewLinkNote] = useState("");



    useEffect(() => {
        const fetchUserData = async () => {
            try {
                // setLoading(true);
                const data = await getUserData(user.uid); //    UID 
                // setUserData(data);
                console.log('data', data);

                set_nofap_timestamp(data.nofap_timestamp)
                if (data.nofap_links) {
                    setLinks(data.nofap_links)

                }
                setIsLoading(false)
            } catch (err) {
                setIsLoading(false)
                console.log('      , nofap_timestamp  ..', err);

                // setError(err.message);
            } finally {
                // setLoading(false);
            }
        };

        fetchUserData();
    }, [isLoading]);


    const handleAddLink = () => {
        let sendItem = { "link": newLink, "note": newLinkNote }

        setIsLoading(true)

        addLinkToUser_nofap(user.uid, sendItem)
            .then(() => {
                console.log("  !");
                setIsLoading(false)
                setNewLink('')
                setNewLinkNote('')
            })
            .catch((error) => {
                console.error("   :", error.message);
                setIsLoading(false)
                setNewLink('')
                setNewLinkNote('')
            });



    };


    const handleRemoveLink = (linkToRemove) => {
        setIsLoading(true)

        removeLinkFromUser_nofap(user.uid, linkToRemove)
            .then(() => {
                console.log("  !");
                setIsLoading(false)
            })
            .catch((error) => {
                console.error("   :", error.message);
                setIsLoading(false)
            });
    };




    const start_nofap_now_function = async () => {
        try {
            const dateNow = Date.now()
            await updateUserData(user.uid, 'nofap_timestamp', dateNow); //     
            set_nofap_timestamp(dateNow)
        } catch (error) {
            console.log('    ,    ....', error);

        }
    };





    const start_nofap_fromdate_function = async () => {
        const createTimestamp = (date, time) => {
            const [hours, minutes] = time.split(":"); //      
            const dateObj = new Date(date); //   Date   
            dateObj.setHours(hours); //  
            dateObj.setMinutes(minutes); //  
            return dateObj.getTime(); //  timestamp ( )
        };


        try {
            const timestamp = createTimestamp(startDate, startTime);
            console.log("Timestamp:", timestamp); // : 1746432120000
            await updateUserData(user.uid, 'nofap_timestamp', timestamp); //     
            set_nofap_timestamp(timestamp)
        } catch (error) {
            console.log('    ,    ....', error);

        }
    };


    const wipe_nofap_now_function = async () => {
        try {
            await updateUserData(user.uid, 'nofap_timestamp', 0); //     
            set_nofap_timestamp(0)
        } catch (error) {
            console.log('    ,    ....', error);
        }
    };


    if (isLoading) {
        return <div><p>   ... ...</p></div>
    }
    else {

        return (
            <div style={{ padding: "20px", fontFamily: "Arial, sans-serif" }}>
                <div
                    className="nofap_container"

                >
                    <div>

                        <h1> </h1>


                        {nofap_timestamp > 0 ?
                            <>





                                <ProgressBar timestamp={nofap_timestamp} />

                                {/*     */}
                                <div style={{ marginBottom: "20px" }}>
                                    <button
                                        onClick={wipe_nofap_now_function}
                                        style={{
                                            marginTop: '50px',
                                            cursor: "pointer",
                                        }}
                                    >
                                         /  
                                    </button>
                                </div>
                            </>
                            :
                            <>
                                <div style={{ marginBottom: "20px" }}>
                                    <h2> </h2>
                                    <div style={{ marginBottom: "10px" }}>
                                        <label>
                                             :
                                            <input
                                                type="date"
                                                value={startDate}
                                                onChange={(e) => setStartDate(e.target.value)}
                                                style={{ marginLeft: "10px" }}
                                            />
                                        </label>
                                    </div>
                                    <div style={{ marginBottom: "10px" }}>
                                        <label>
                                             :
                                            <input
                                                type="time"
                                                value={startTime}
                                                onChange={(e) => setStartTime(e.target.value)}
                                                style={{ marginLeft: "10px" }}
                                            />
                                        </label>
                                    </div>

                                    <button onClick={start_nofap_now_function} style={{ padding: "5px 10px" }}>
                                          
                                    </button>
                                    <button
                                        onClick={start_nofap_fromdate_function}
                                        style={{ padding: "5px 10px", marginLeft: "10px" }}
                                    >
                                         
                                    </button>
                                </div>
                            </>
                        }
                    </div>

                    {/*     */}
                    <div style={{ marginBottom: '50px', }}>
                        <h2> </h2>
                        <div style={{ marginBottom: "10px" }}>
                            <input
                                type="text"
                                placeholder=" "
                                value={newLink}
                                onChange={(e) => setNewLink(e.target.value)}
                                style={{ marginRight: "10px", padding: "5px" }}
                            />
                            <input
                                type="text"
                                placeholder=" "
                                value={newLinkNote}
                                onChange={(e) => setNewLinkNote(e.target.value)}
                                style={{ marginRight: "10px", padding: "5px" }}
                            />
                            <button onClick={handleAddLink} style={{ padding: "5px 10px" }}>
                                
                            </button>
                        </div>
                        {/* <ul> */}
                        {links.map((link, index) => (
                            // <li >
                            <div key={index} className="nofap_link">
                                <a href={link.link} target="_blank" rel="noopener noreferrer">
                                    {link.note}
                                    {/*  */}
                                </a>
                                <button onClick={() =>
                                    handleRemoveLink(link)
                                }>
                                    
                                </button>

                            </div>
                            // </li>
                        ))}
                        {/* </ul> */}
                    </div>
                </div>



            </div>
        );
    }

};

export default NoFapPage;