const app = initializeApp(firebaseConfig);
const auth = getAuth(app); //  
const provider = new GoogleAuthProvider(); //   Google
const db = getFirestore(app);
export const listenToAuthChanges = (callback) => {
    onAuthStateChanged(auth, (user) => {
        callback(user); //     callback
    });
};


export const getUserData = async (uid) => {
    try {
        if (!uid) {
            throw new Error("UUID   ");
        }

        //        "users"
        const userRef = doc(db, "users", uid);

        //   
        const userDoc = await getDoc(userRef);

        //   ,   
        if (userDoc.exists()) {
            console.log(" :", userDoc.data());
            return userDoc.data();
        } else {
            console.log("  ");
            return null;
        }
    } catch (error) {
        console.error("    :", error.message);
        throw error; //   
    }
};



export const updateUserData = async (userId, fieldName, value) => {
    try {
        console.log(userId, fieldName, value);

        //        "users"
        const userRef = doc(db, "users", userId);

        //       
        await setDoc(
            userRef,
            { [fieldName]: value }, //   
            { merge: true } //   ,   ,   
        );

        console.log(` "${fieldName}"     ${userId}`);
    } catch (error) {
        console.error("    :", error.message);
        throw error; //   
    }
};


export const addLinkToUser_nofap = async (userId, linkData) => {
    try {
        //        "users"
        const userRef = doc(db, "users", userId);

        //     nofap_links
        await updateDoc(userRef, {
            nofap_links: arrayUnion(linkData), //     
        });

        console.log(`     ${userId}`);
    } catch (error) {
        console.error("   :", error.message);
        throw error; //   
    }
};


export const removeLinkFromUser_nofap = async (userId, linkData) => {
    try {
        //        "users"
        const userRef = doc(db, "users", userId);

        //     nofap_links
        await updateDoc(userRef, {
            nofap_links: arrayRemove(linkData), //     
        });

        console.log(`     ${userId}`);
    } catch (error) {
        console.error("   :", error.message);
        throw error; //   
    }
};

