import React from "react";
import { BrowserRouter, Routes, Route, Navigate, Link } from "react-router-dom";
import { useSelector } from "react-redux";

// 
import Login from "../components/Login";
import NoFapPage from "../pages/NoFap__page";
import CountMoneyPage from "../pages/CountMoney__page";
import { getUserData } from "../services/firebase";
import Logout from "../pages/Logout__page";


const Header = ({ routes }) => {
    const user = useSelector((state) => state.auth.user); //   

    //    
    const visibleRoutes = routes.filter(
        (route) => !route.isPrivate || user
    );

    //     
    const [currentTime, setCurrentTime] = React.useState(new Date());

    //      
    React.useEffect(() => {
        const intervalId = setInterval(() => {
            setCurrentTime(new Date());
        }, 1000);

        //     
        return () => clearInterval(intervalId);
    }, []);

    //      
    const formatDateTime = (date) => {
        const day = String(date.getDate()).padStart(2, "0");
        const month = String(date.getMonth() + 1).padStart(2, "0"); //    0
        const year = date.getFullYear();
        const hours = String(date.getHours()).padStart(2, "0");
        const minutes = String(date.getMinutes()).padStart(2, "0");

        return `${day}.${month}.${year} ${hours}:${minutes}`;
    };

    return (
        <div className="nav_wrap">
            {/*    */}

            {!user ? (
                <p className="nav_guest">   ... ...</p>
            ) : (
                <div>
                    <Link to="/">
                        <button className="nav_link"></button>
                    </Link>
                </div>
            )}
            <div className="current-time">{formatDateTime(currentTime)}</div>
        </div>
    );
};



// import React from 'react'
const MainPage = () => {
    const user = useSelector((state) => state.auth.user); //   

    const routes = routeConfig.filter((item) => item.path !== '/')

    //    
    const visibleRoutes = routes.filter(
        (route) => !route.isPrivate || user
    );


    React.useEffect(() => {
        const fetchUserData = async () => {
            try {
                // setLoading(true);
                const data = await getUserData(user.uid); //    UID 
                console.log('data', data);

                // setUserData(data);
            } catch (err) {
                // setError(err.message);
            } finally {
                // setLoading(false);
            }
        };

        fetchUserData();
    }, []);

    return (

        <div style={{
            padding: '10px'
        }}>

            {/*    */}
            {user && (
                <div style={{ marginBottom: "10px" }}>
                    <p><strong>Email:</strong> {user.email}</p>
                    <p><strong>:</strong> {user.displayName}</p>
                    {/* <p><strong>uid:</strong> {user.uid}</p> */}


                    {user.photoURL && (
                        <img
                            src={user.photoURL}
                            alt="Avatar"
                            style={{ width: "50px", borderRadius: "50%" }}
                        />
                    )}
                </div>
            )}



            <div>

                <div style={{ display: "flex", flexDirection: 'column', gap: "10px" }}>

                    {visibleRoutes.map((route, index) => {
                        if (user && route.isVisibleWhenLoggined === true) {
                            return (
                                <Link
                                    to={route.path}

                                >
                                    <button key={index} className="nav_link">
                                        {route.label || route.path}

                                    </button>
                                </Link>


                            )
                        }
                        else if (!user && route.isVisibleWhenLoggined === false) {
                            return (
                                <div key={index}>
                                    <Link
                                        to={route.path}
                                        style={{
                                            textDecoration: "none",
                                            color: "blue",
                                            padding: "5px 10px",
                                            border: "1px solid #ccc",
                                            borderRadius: "5px",
                                            backgroundColor: "#fff",
                                        }}
                                    >
                                        {route.label || route.path}
                                    </Link>
                                </div>

                            )
                        }
                        else {
                            return (
                                <div key={index}>

                                </div>

                            )
                        }


                    }
                    )}
                </div>
            </div>



        </div>


        // </header>
    );
};


//  
const routeConfig = [
    {
        path: "/login",
        element: <Login />,
        isPrivate: false, //   
        isVisibleWhenLoggined: false,
        label: "", //    Header
    },
    {
        path: "/logout",
        element: <Logout />,
        isPrivate: true, //   
        isVisibleWhenLoggined: true,
        label: "", //    Header
    },

    {
        path: "/nofap_page",
        element: <NoFapPage />,
        isPrivate: true, //  
        isVisibleWhenLoggined: true,
        label: " ",
    },

    {
        path: "/",
        element: <MainPage />,
        isPrivate: true, //  
        isVisibleWhenLoggined: true,
        label: "",
    },
    {
        path: "/countmoney_page",
        element: <CountMoneyPage />,
        isPrivate: true, //  
        isVisibleWhenLoggined: true,
        label: " ",
    },
    // {
    //     path: "*",
    //     element: <Navigate to="/login" />, //    
    //     isPrivate: false,
    // },
];



// const PrivateRoute = ({ children, isPrivate }) => {
//     const user = useSelector((state) => state.auth.user);
//     return isPrivate ? (user ? children : <Navigate to="/login" />) : children;
// };

const PrivateRoute = ({ children, isPrivate, path }) => {
    const user = useSelector((state) => state.auth.user);
    if (isPrivate && !user) {
        return <Navigate to="/login" />;
    } else if (!isPrivate && user && path === '/login') {
        return <Navigate to="/" />;
    }
    return children;
};

