// export default AppRoutes;

const AppRoutes = () => {
    return (
        <BrowserRouter>
            <div className="BrowserRouter_wrap">
                <Header routes={routeConfig.filter((route) => route.label)} />

                {/*  */}
                <Routes>
                    {routeConfig.map((route, index) => (
                        <Route
                            key={index}
                            path={route.path}
                            element={
                                <PrivateRoute
                                    isPrivate={route.isPrivate}
                                    path={route.path}
                                >
                                    {route.element}
                                </PrivateRoute>
                            }
                        />
                    ))}
                </Routes>

            </div>
        </BrowserRouter>
    );
};

export default AppRoutes;